@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;500;700;900&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

*{padding:0;margin:0;box-sizing:border-box;font-family:'Roboto', sans-serif;font-weight:300;scroll-behavior:smooth}h1,h2{font-weight:500}.app{display:flex}

*{padding:0;margin:0;box-sizing:border-box}.sidebar{position:relative;transition-timing-function:ease;transition:0.5s;display:flex;flex-direction:column;justify-content:space-between;align-items:center;width:400px;height:100vh;background:linear-gradient(180deg, #1c3966 0%, #04142b 100%);position:fixed}.sidebar--menu{display:flex;flex-direction:column;justify-content:center;align-items:center;height:100%}.sidebar--menu li{color:#ececec;list-style:none;margin:20px}.sidebar--menu a{color:#ececec;text-decoration:none;font-size:18px}.sidebar--social{z-index:1;width:100%;background-color:#071427;height:120px;display:flex;justify-content:center;align-items:center}.sidebar--social img{transition-timing-function:ease;transition:0.5s;height:40px;margin:10px}@media only screen and (max-width: 1200px){.sidebar{width:250px}.sidebar--social img{height:35px;margin:7px}}@media only screen and (max-width: 800px){.sidebar{transition-timing-function:ease;transition:0.5s;transform:translateX(-100%)}}

.button{border:none;min-width:150px;text-align:center;padding:15px;color:white;cursor:pointer;transition:0.25s}.button:focus,.button:hover{box-shadow:inset 300px 0 0 0 #9e2a2a}.red{background-color:#C23939}a{text-decoration:none}.w300{width:300px}

.skill--container{flex:1 1;min-width:50%;padding:10px 20px 10px 0}.skill--container--last{max-width:50%}.skill--progress{width:100%;height:5px;background-color:#d4d4d4}.progress--10{width:10%;height:100%}.progress--20{width:20%;height:100%}.progress--30{width:30%;height:100%}.progress--40{width:40%;height:100%}.progress--50{width:50%;height:100%}.progress--60{width:60%;height:100%}.progress--70{width:70%;height:100%}.progress--80{width:80%;height:100%}.progress--90{width:90%;height:100%}.progress--100{width:100%;height:100%}.progress--red{background-color:#C23939}.progress--blue{background-color:#0D2344}dfn{cursor:help;font-style:normal;position:relative}dfn::after{content:attr(data-info);display:inline;position:absolute;top:22px;right:40px;opacity:0;width:230px;font-size:14px;font-weight:500;line-height:1.5em;padding:0.5em 0.8em;background:rgba(0,0,0,0.8);color:#fff;pointer-events:none;transition:opacity 250ms, top 250ms}dfn::before{content:'';display:block;position:absolute;top:12px;right:80px;opacity:0;width:0;height:0;border:solid transparent 5px;border-bottom-color:rgba(0,0,0,0.8);transition:opacity 250ms, top 250ms}dfn:hover{z-index:2}dfn:hover::after,dfn:hover::before{opacity:1}dfn:hover::after{top:45px}dfn:hover::before{top:35px}@media only screen and (max-width: 800px){dfn::after{width:150px}}

.contact{display:flex;justify-content:space-between;flex-wrap:wrap}.contact--input--box{position:relative;margin:0 0 35px 0}.contact--input--box input,.contact--input--box textarea{width:100%;padding:5px 0;font-size:18px;font-weight:300;border:none;border-bottom:1px solid #b9b9b9;outline:none}.contact--input--box textarea{resize:vertical;min-height:120px}.contact--input--box span{position:absolute;left:0;padding:5px 0;font-size:18px;font-weight:300;transition:0.5s;pointer-events:none}.contact--input--box input:focus ~ span,.contact--input--box textarea:focus ~ span,.contact--input--box input:valid ~ span,.contact--input--box textarea:valid ~ span{transform:translateY(-20px);font-size:12px;color:#C23939;font-weight:500}.w50{width:47%}.w100{width:100%}@media only screen and (max-width: 800px){.w50{width:100%}}

*{padding:0;margin:0;box-sizing:border-box;font-family:'Roboto', sans-serif}hr{height:1px;background-color:#475b92;border:none}.main--content{transition-timing-function:ease;transition:0.5s;margin-left:400px;display:flex;flex-direction:column;justify-content:flex-end}.main--container{max-width:1200px;padding:20px 100px}.main--container h1{display:inline-block;padding-bottom:10px;border-bottom:1.5px solid #C23939;margin-bottom:20px}.main--container h2{margin-top:20px;margin-bottom:10px}.main--container--skills{display:flex;flex-wrap:wrap}.main--hero{height:650px;max-width:1200px;display:flex;flex-direction:column;justify-content:flex-end;padding-left:100px;padding-bottom:100px;background-image:url(/static/media/profile_large.b9d188e5.png);background-repeat:no-repeat;background-position:bottom right 80px}.main--hero h4{color:#C23939;font-weight:300;font-size:20px}.main--hero h1{font-size:40px}.main--hero h2{font-size:30px;margin-bottom:40px}.main--hero--break{width:50px;border-bottom:1.5px solid #C23939;margin-top:50px;margin-bottom:50px}.education{display:flex;flex-wrap:wrap}.education--block{min-width:250px;flex:1 1;padding-bottom:30px;padding-right:40px}.education--block h2{font-size:22px;padding-bottom:5px;margin:0}.education--block h3{font-size:16px;font-weight:500;color:#C23939}.education--block h4{font-size:14px;padding-bottom:10px}.education--block p strong{font-weight:500}.break{width:150px;height:1px;border-bottom:1px solid #C23939;margin-bottom:15px}strong{font-weight:500}a{text-decoration:none;color:#000}.subtitle{font-size:14px;color:#8f8f8f;margin-top:-10px;margin-bottom:10px}@media only screen and (max-height: 700px){.main--hero{height:100vh}}@media only screen and (max-width: 1200px){.main--content{margin-left:250px}.main--hero h2{width:300px;font-size:30px;margin-bottom:40px;background-color:rgba(255,255,255,0.6);padding:0 10px}.main--hero--break{margin-top:100px;margin-bottom:50px}}@media only screen and (max-width: 800px){.main--content{margin-left:0}.main--container{padding:20px 40px}.main--hero{padding-left:0;padding-bottom:0;align-items:center;background-position:bottom center;padding-bottom:20px}.main--hero h4{background-color:rgba(255,255,255,0.6);padding:0 10px}.main--hero h1{background-color:rgba(255,255,255,0.6);padding:0 10px}.main--hero h2{font-size:30px;margin-bottom:40px;background-color:rgba(255,255,255,0.6);padding:0 10px}.main--hero--break{width:50px;border-bottom:1.5px solid #C23939;margin-top:20px;margin-bottom:20px}}

.edu--timeline ul li{list-style-type:none;position:relative;width:3px;padding-bottom:30px;background:#C23939}.edu--timeline ul li::after{content:'';position:absolute;left:50%;top:5px;transform:translateX(-50%);width:20px;height:20px;border-radius:50%;background:inherit}.edu--timeline ul li div{position:relative;width:800px;padding:15px;background:#ececec;left:50px}.edu--timeline ul li div::before{content:'';position:absolute;top:5px;width:0;height:0;border-style:solid;left:-15px;border-width:8px 16px 8px 0;border-color:transparent #ececec transparent transparent}.edu--education{display:flex;flex-direction:column}.edu--education h2{margin:0;padding:0;font-size:18px;font-weight:bold;margin-bottom:8px}.edu--education h3{margin:0;padding:0;font-size:16px;font-weight:bold;margin-bottom:8px}.edu--education h4{font-size:16px;font-weight:bold;margin-top:10px}.edu--education .edu--skills{line-height:30px}.edu--education .edu--skills strong{padding:2px 5px;background-color:#fff;border-radius:10px}@media only screen and (max-width: 1200px){.edu--timeline ul li div{width:600px}}@media only screen and (max-width: 800px){.edu--timeline ul li div{width:500px}}@media only screen and (max-width: 600px){.edu--timeline ul li div{width:280px}}

.exp--timeline ul li{list-style-type:none;position:relative;width:3px;padding-bottom:30px;background:#C23939}.exp--timeline ul li::after{content:'';position:absolute;left:50%;top:5px;transform:translateX(-50%);width:20px;height:20px;border-radius:50%;background:inherit}.exp--timeline ul li div{position:relative;width:800px;padding:15px;background:#ececec;left:50px}.exp--timeline ul li div::before{content:'';position:absolute;top:5px;width:0;height:0;border-style:solid;left:-15px;border-width:8px 16px 8px 0;border-color:transparent #ececec transparent transparent}.exp--experience{display:flex;flex-direction:column}.exp--experience h2{margin:0;padding:0;font-size:18px;font-weight:bold;margin-bottom:8px}.exp--experience h3{margin:0;padding:0;font-size:14px;font-weight:bold;margin-bottom:8px}@media only screen and (max-width: 1200px){.exp--timeline ul li div{width:600px}}@media only screen and (max-width: 800px){.exp--timeline ul li div{width:400px}}@media only screen and (max-width: 600px){.exp--timeline ul li div{width:280px}}

.portfolio--image{height:150px;margin:5px;box-shadow:3px 3px 3px rgba(0,0,0,0.3);border:1px solid #b9b9b9;-webkit-filter:grayscale(80%);cursor:pointer;filter:grayscale(80%);opacity:0.5;transition:0.5s}.portfolio--image:hover{filter:grayscale(0%);-webkit-filter:grayscale(0%);opacity:1}.portfolio--item{display:flex;flex-direction:column;margin-bottom:50px}.portfolio--item a:hover{color:#C23939}.portfolio--item--wrap{padding-left:30px;border-left:1px solid #b9b9b9}.portfolio--item--text{border-left:4px solid #C23939;padding-left:30px}.portfolio--item--text h3{font-weight:500;margin-bottom:5px}.portfolio--item--text p{margin-bottom:5px}.portfolio--item--features ul{margin-bottom:10px;font-weight:500}.portfolio--item--features ul li{margin-left:20px;list-style-type:square}.portfolio--item--images{display:flex;flex-wrap:wrap;margin-bottom:10px}.portfolio--item--technology{max-width:500px;display:flex;justify-content:space-between}.portfolio--icon{height:50px;margin:10px 15px 10px 0}.portfolio--technology{margin-top:5px}.portfolio--technology strong{color:white;padding:2px 10px;border-radius:10px;background-color:#838383}

